        flex-direction: column;
        align-items: center;
         margin-bottom: 0.8rem;
    }
     .NoPBox{
        width: 33rem;
        height: 18rem;
        display: flex;
        flex-direction: column;
        align-items: center;
         margin-top: 0.8rem;
    }
    dt {
        padding: 1rem;
