        margin: 0 0.6rem;
        color: rgb(126, 122, 122);
        border-bottom: 2px solid rgb(207, 199, 199);
        /* border-radius: 10px; */
        margin-bottom: 0.8rem;
    }
    .dd{
        margin-top: 2%;
        height: 28rem;
        margin-left: 2%;
        margin-right: 2%;
        background-color: rgb(248, 248, 248);
        overflow: auto;
        border: 0.08rem solid rgb(163, 159, 159);
        border-radius: 2px;
        -ms-user-select: none; 
       -moz-user-select: -moz-none;
       -khtml-user-select: none;
       -webkit-user-select: none;
        user-select: none;
    }
    .ddTitle{
        font-size: 1.1rem;
        font-weight: 510;
        padding-top: 0.5rem;
    }
    .ddSubTitle{
        font-size: 0.89rem;
        font-weight: 500;;
    }

    .ddSubContent{
        font-size: 0.75rem;
        margin-top: 0.6rem;
        margin-left: 1rem;
    }
    .agreeCheckBox{
      margin-top: 0.5rem;
      -ms-user-select: none; 
      -moz-user-select: -moz-none;
      -khtml-user-select: none;
      -webkit-user-select: none;
       user-select: none;
    } 
    .inputItem{
        padding: 0rem 1.7rem;
        display: flex;
        flex-direction: row;
    }
    .inputItem span{
        font-size: 1.5rem;
        color: rgb(126, 122, 122);
        margin: 0rem 0.5rem;
    }
    input{
        width: 20rem;
        height: 3rem;
        /* color: rgb(126, 122, 122); */
        font-size: 1.5rem;
    }  
    .error{
        border-color:#e70448;
        outline:none;
    }
    .form-control{
        display: inline !important;
    }
    .inline{
       display: inline !important;
    }
    .errorMessage{
       
        font-size: 0.75rem !important;
        color: rgb(216, 16, 16) !important;;
        margin-bottom: 0;
    }
    .wrongNumberLine{
        border: 0.1rem solid rgb(216, 16, 16) ;
        border-radius: 0.25rem;
        padding: 0.72rem 0.12rem;
        padding-top: 1.2rem;

    }

    @media (max-width: 798px) {
        .agreementForCollection{
          
        }
        .left_box{
            margin: 0 0;
            margin-bottom: 1rem;
        }

        .NoPBox{
            width: 100%;
            margin: 0 0;
            margin-bottom: 1rem;
        }
        .inputBox{
            width: 100%;
            margin: 0 0;
            margin-bottom: 1rem;
            height: 20rem;
        }
        .inputItem{
            padding: 0 0 ;
        }
        .inputItem span{
            
        }
        #input-name{
            width: 10rem;
        }
        #input-num{
            width: 3rem;
        }
        .label{
            width: 4rem;
           
        }
        .label span{
             font-size: 1rem;
             width: 4rem;
           
        }

    }    


    
</style>